<template>
    <div class="pages min-h-screen flex flex-col">
        <Nav />

        <div class="flex flex-1">
            
            <Sidebar />

            <MainContent>
                <Title :title="title.name" />
                <Info />

                <router-view></router-view>

                <section class="content-section grid gap-6 mb-8 md:grid-cols-2 xl:grid-cols-3">
                    <BoxStat 
                        bg-color="bg-green-200" 
                        text-color="text-green-600" 
                        title="Published"
                        stat="10"
                        icon="file"
                    />

                    <BoxStat 
                        bg-color="bg-blue-200" 
                        text-color="text-blue-600" 
                        title="Draft"
                        stat="5"
                        icon="file-alt"
                    />

                    <BoxStat 
                        bg-color="bg-red-200" 
                        text-color="text-red-600" 
                        title="Deleted"
                        stat="5"
                        icon="trash-alt"
                    />

                </section>

                <section class="content-section overflow-x-auto">
                    <div class="overflow-hidden rounded-lg shadow-xs w-full">
                        <Table :table-data="pages" :table-header="pageHeader" />
                        <Pagination />
                    </div>
                </section>
            </MainContent>
        </div>
    </div>
</template>

<script>
import { useStore } from 'vuex';
import { useRoute } from 'vue-router';
import { computed } from 'vue';
import Nav from '@/components/Dashboard/Header/Nav';
import Sidebar from '@/components/Dashboard/Sidebar/Sidebar';
import Title from '@/components/Dashboard/Content/Title';
import Info from '@/components/Dashboard/Content/Info';
import MainContent from '@/components/Dashboard/Content/MainContent';
import BoxStat from '@/components/Dashboard/Content/BoxStat';
import Table from '@/components/Common/Table';
import Pagination from '@/components/Common/Pagination';

export default {
    name: 'Pages',
    components: {
        Nav,
        Sidebar,
        Title,
        Info,
        MainContent,
        BoxStat,
        Table,
        Pagination
    },

    setup() {
        const route = useRoute();

        let title = computed(() => {
            console.log(route)
            return route;
        });

        const store = useStore();

        const pages = computed(() => {
            return store.getters['pages/getPages'];
        })

        const pageHeader = [
            { name: '_id' },
            { name: 'title' },
            { name: 'author' },
            { name: 'status' },
        ]

        return {
            title,
            pages,
            pageHeader
        }
    }
}
</script>

